.background {
    width: 100vw;
    height: 100vh;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #101010;
  }

  .containerBg {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    position: relative;
    overflow: hidden;
}
.bgRight {
  background-image: url('../../images/retinaImg/dollars_bg_left@1x.png');
  flex: 1;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-device-pixel-ratio: 2),
(-webkit-min-device-pixel-ratio: 2),
(min-resolution: 192dpi),
(min-resolution: 2dppx) {
  .bgRight {
    background-image: url('../../images/retinaImg/dollars_bg_left@2x.png');
}

}
.bgLeft {
  background-image: url('../../images/retinaImg/dollars_bg_right@1x.png');
  flex: 2;
  background-repeat: no-repeat;
  /* background-position: right; */
  background-size: cover;
}
@media (min-device-pixel-ratio: 2),
(-webkit-min-device-pixel-ratio: 2),
(min-resolution: 192dpi),
(min-resolution: 2dppx) {
  .bgLeft {
    background-image: url('../../images/retinaImg/dollars_bg_right@2x.png');
}
}
.responsiveImg{
  position: absolute;
  border-radius: 100%;
}
